@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }

  body {
    @apply bg-gray-50 text-gray-900;
  }

  h1, h2, h3, h4, h5, h6 {
    @apply font-bold;
  }
}

@layer components {
  .section-container {
    @apply container mx-auto px-4 py-8;
  }

  .section-title {
    @apply text-3xl font-bold mb-6 text-gray-900;
  }

  .card-hover {
    @apply transition-transform duration-300 hover:scale-105;
  }
}